* {
  margin: 0px;
  padding: 0px;
}
#root {
  height: 100vh;
}

html,
body {
  min-height: 100vh;
  height: 100vh;

}

span {
  font-size: 40px
}

.dark-mode {
  background-color: #2196F3;
  color: #999;
  height: 100%;
  transition: background-color 0.25s ease-in-out;
}

.light-mode {
  position: relative;
  background: url('./assets/img/day.jpg');
  background-repeat: repeat-x;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  transition: background-color 0.25s ease-in-out;
  animation: animate 10s linear infinite;

}

@keyframes moveLeft {
  100%{
    transform: translateX(-100%);
  }
}

@keyframes animate {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -100% 0;
  }
}

.container {
  display: flex;
  justify-content: center;
  align-items: center
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 14px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #19a2c1;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
.switch-checkbox {
  padding: 10px
}